/* eslint-disable react/no-danger */
import { useEffect, useMemo } from 'react';
import {
  actions,
  GET_DETAIL_PROJECT,
  GET_INFO_USER,
} from '@containers/Home/slice';
import { useRouter } from 'next/router';
import { useDispatch, useSelector } from 'react-redux';
import { NextSeo } from 'next-seo';
import {
  selectConfigLittlePlanetProject,
  selectConfigFirstSceneProject,
  selectMetaDataProject,
  selectDetailProject,
  selectTitleProject,
  selectListScene,
  selectToken,
} from '@containers/Home/selectors';
import {
  LIST_FAVICON_DEFAULT,
  LIST_LINK_STYLE_SHEET,
} from '@containers/Home/constants';
import { END } from 'redux-saga';
import { SagaStore, wrapper } from 'store';
import dynamic from 'next/dynamic';
import { parse } from 'cookie';
import { KEY_COOKIE } from '@type/customType';

const RequestAccessCollaboration = dynamic(
  () =>
    import(
      '@containers/Home/components/Collaboration/RequestAccessCollaboration'
    ),
);
const Home = dynamic(() => import('containers/Home'), {
  loading: () => <span>Home Loading</span>,
});

interface Props {
  isRedirectToTokenPage?: boolean;
  isAllowToAccessOnCollaborationMode: boolean;
  // NOTE: get from cookie
  passwordTourCookie?: string;
  listProjectGuardOpenedCookie?: string;
  listFormCaptureOpenedCookie?: string;
}

function CollaborationContainer({
  isRedirectToTokenPage = false,
  isAllowToAccessOnCollaborationMode,

  passwordTourCookie,
  listProjectGuardOpenedCookie,
  listFormCaptureOpenedCookie,
}: Props) {
  const router = useRouter();
  const dispatch = useDispatch();

  const token = useSelector(selectToken);
  const title = useSelector(selectTitleProject);
  const listScene = useSelector(selectListScene);
  const metadata = useSelector(selectMetaDataProject);
  const detailProject = useSelector(selectDetailProject);
  const first_scene = useSelector(selectConfigFirstSceneProject);
  const config_little_planet = useSelector(selectConfigLittlePlanetProject);

  const detailFirstScene = useMemo(
    () => listScene.filter(item => item.id === first_scene)[0] || undefined,
    [listScene, first_scene],
  );

  useEffect(() => {
    if (!token && router.isReady && isRedirectToTokenPage)
      window.open(
        `${process.env.NEXT_PUBLIC_BASE_URL_STUDIO}/redirect${
          process.env.NEXT_PUBLIC_BASE_URL_COLLABORATION
        }/${router.query.tour}/${router.query.scene || ''}`,
        '_self',
      );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [router.isReady, isRedirectToTokenPage]);

  useEffect(() => {
    if (listScene && listScene.length) {
      let scene = detailFirstScene
        ? { ...detailFirstScene }
        : { ...listScene[0] };
      if (router.query.scene)
        scene = {
          ...listScene.filter(item => item.id === router.query.scene)[0],
        } || {
          ...listScene[0],
        };

      // NOTE: add intro little planet for first scene loaded
      scene.config = { ...scene?.config } || {};
      scene.config = {
        ...scene.config,
        isLittlePlanet: config_little_planet?.enable || false,
        timeTransitionLittlePlanet:
          config_little_planet?.transition_time || 2.0,
      };
      dispatch(actions.setSceneSelected(scene));
      if (isAllowToAccessOnCollaborationMode)
        dispatch(
          actions.setIsAllowToAccessOnCollaborationMode(
            isAllowToAccessOnCollaborationMode,
          ),
        );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [listScene.length]);

  return (
    <>
      {token && (
        <>
          <NextSeo
            title={metadata?.title || title}
            description={metadata?.description || 'Panoee - Virtual Tour 360'}
            additionalLinkTags={
              metadata?.favicon
                ? [
                    ...[
                      {
                        rel: 'shortcut icon',
                        href: metadata?.favicon,
                      },
                    ],
                    ...LIST_LINK_STYLE_SHEET,
                  ]
                : [...LIST_FAVICON_DEFAULT, ...LIST_LINK_STYLE_SHEET]
            }
          />
          {isAllowToAccessOnCollaborationMode ? (
            <Home
              isPublic
              isCollaborationMode
              passwordTourCookie={passwordTourCookie}
              listProjectGuardOpenedCookie={listProjectGuardOpenedCookie}
              listFormCaptureOpenedCookie={listFormCaptureOpenedCookie}
            />
          ) : (
            <RequestAccessCollaboration
              project_id={detailProject?.id || ''}
              scene={detailFirstScene}
            />
          )}
        </>
      )}
    </>
  );
}

CollaborationContainer.getInitialProps = wrapper.getInitialPageProps(
  store =>
    async ({ query, req }) => {
      const cookies = parse(req.headers.cookie || '');
      const cookieProps = {
        listProjectGuardOpenedCookie: cookies
          ? cookies[KEY_COOKIE.LIST_PROJECT_PASS_GUARD] || null
          : null,
        passwordTourCookie: cookies
          ? cookies[KEY_COOKIE.PASSWORD_TOUR] || null
          : null,
        listFormCaptureOpenedCookie: cookies
          ? cookies[KEY_COOKIE.LIST_FORM_CAPTURE_OPENED] || null
          : null,
      };
      if (query.tour && query.token) {
        store.dispatch(
          actions.setToken(JSON.parse(JSON.stringify(query.token))),
        );
        store.dispatch(GET_DETAIL_PROJECT({ id: query.tour, isPublic: true }));
        store.dispatch(GET_INFO_USER({ token: query.token }));
        store.dispatch(END);
        await (store as SagaStore).sagaTask?.toPromise();
        const { app } = store.getState();

        const isOwnerTour =
          app.infoUser && app.project
            ? app.infoUser.id === app.project.user
            : false;
        const isAllowToAccessOnCollaborationMode =
          isOwnerTour ||
          (app.configCollaboration?.isEnabled &&
            (app.configCollaboration?.isPublic ||
              `${app.configCollaboration?.collaborations} `
                .split(/\r?\n/)
                .map(item => item.trim())
                .filter(item => !!item)
                .indexOf(app.infoUser?.email) > -1));
        return {
          isRedirectToTokenPage: false,
          isAllowToAccessOnCollaborationMode,
          ...cookieProps,
        };
      }
      return {
        isRedirectToTokenPage: true,
        isAllowToAccessOnCollaborationMode: false,
        ...cookieProps,
      };
    },
);

export default CollaborationContainer;
